<template>
  <div id="shopInfo" v-if="Object.keys(shopInfo).length!==0">
    <div class="shop-top">
      <img :src="shopInfo.logo" alt="">
      <span>{{shopInfo.name}}</span>
      
    </div>
    <div class="shop-bot">
      <div class="bot-left">
        <div class="left1">
          <div class="sells">{{shopInfo.sells | moneyFormat}}</div>
          <div class="sells-text">总销量</div>
        </div>
        <div class="left2">
          <div class="sells">{{shopInfo.goods | moneyFormat}}</div>
          <div class="sells-text">全部宝贝</div>
        </div>
      </div>
      <div class="bot-center"></div>
      <div class="bot-right">
        <div>
          <table class="score">
            <tr v-for="(item,index) in shopInfo.score" :key="index">
              <td>{{shopInfo.score[index].name}}</td>
              <td class="false" :class="{'true' : shopInfo.score[index].isBetter}">{{shopInfo.score[index].score}}</td>
              <td class="gd-f" :class="{'gd-t' : shopInfo.score[index].isBetter}"><span>{{shopInfo.score[index].isBetter ? "高" : "低"}}</span></td>
            </tr>
          </table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name:'detailShopInfo',
  data() {
    return {
      
    }
  },
  components: {
    
  },
  props:{
    shopInfo:{
      type:Object,
      default(){
        return {}
      }
    }
  },
  created(){

  },
  filters:{ //过滤
		//取截单元,单位
		moneyFormat:function(arg){
			if(arg.toString().length>=13){
				// return arg/1000000000000+"万亿"
				const moneys = arg/1000000000000
				const realVal = parseFloat(moneys).toFixed(2);
				return realVal+"万亿"
				
			}else if(arg.toString().length>=9){
				const moneys = arg/100000000
				const realVal = parseFloat(moneys).toFixed(2);
				return realVal+"亿"
			}else if(arg.toString().length>=4){
				const moneys = arg/10000
				const realVal = parseFloat(moneys).toFixed(1);
				return realVal+"万"
			}else if(arg.toString().length>=1){
				const moneys = arg
				const realVal = parseFloat(moneys).toFixed(0);
				return realVal
			}
			
		}
  },
  mounted(){
    

  },
  methods:{
  },
  activated() {
  },
  watch: {
  },

}
</script>

<style scoped>
  .shop-top{
    padding: 15px 10px;
    display: flex;
    justify-content: start;
    align-items: center;
  }
  .shop-top img{
    width: 50px;
    height: 50px;
    border-radius: 50%;
    border: 1px solid #ccc;
    margin-right: 10px;
  }
  .shop-bot{
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding-bottom: 20px;
    border-bottom: 3px solid #eee;
  }
  .shop-bot .bot-left{
    text-align: center;
    display: flex;
    justify-content: center;
  }
  .shop-bot .bot-left .left1{
    margin-right: 20px;
  }
  .shop-bot .bot-left .sells{
    font-size: 20px;
    margin-bottom: 10px;
  }
  .shop-bot .bot-left .sells-text{
    font-size: 14px;
  }
  .bot-center{
    width: 1px;
    height: 50px;
    background-color: #ccc;
  }
  .score{
    font-size: 14px;
  }
  .score td{
    padding: 3px 5px;
  }
  .score td.false{
    color: rgb(84, 216, 67);
  }
  .score td.false.true{
    color: rgb(255, 77, 77);
  }
  .gd-f span{
    color: #fff;
    padding: 0px 2px;
    background-color: rgb(84, 216, 67);
  }
  .gd-f.gd-t span{
    background-color: rgb(255, 77, 77);
  }
  
</style>
