import {request} from './request'

export function getDetail(iid){
  return request({
    url: 'detail',
    params:{
      iid
    }
  })
}

export class GoodsInfo {  //数据的整合
  constructor(itemInfo,columns,services){
    this.title = itemInfo.title
    this.desc = itemInfo.desc
    this.newPrice = itemInfo.price
    this.oldPrice = itemInfo.oldPrice
    this.discout = itemInfo.discountDesc
    this.discoutBgC = itemInfo.discountBgColor
    this.columns = columns
    this.services = services
    this.realPrice = itemInfo.lowNowPrice
  }
}

export class shopInfo {  //数据的整合
  constructor(shopInfo){
    this.url = shopInfo.shopUrl  //店铺主页
    this.sells = shopInfo.cSells  //销量
    this.goods = shopInfo.cGoods  //商品总数
    this.name = shopInfo.name //店铺名字
    this.logo = shopInfo.shopLogo  //店铺LOGO
    this.score = shopInfo.score   //店铺评分
  }

}