<template>
  <div id="detaill">
    <detail-nav-bar></detail-nav-bar>
    <detail-swiper :detail-banner='detailBanner' />
    <detail-base-info :detail-base-info='detailBaseInfo'/>
    <detail-shop-info :shop-info='shopInfo'/>
    {{iid}}
  </div>
</template>

<script>
import detailNavBar from './childComps/detailNavBar'
import detailSwiper from './childComps/detailSwiper';import {iniSwiper} from 'components/common/swiper'
import detailBaseInfo from './childComps/detailBaseInfo'
import detailShopInfo from './childComps/detailShopInfo'

import {getDetail,GoodsInfo,shopInfo} from 'network/detail'
import DetailShopInfo from './childComps/detailShopInfo.vue';


export default {
  name:'detail',
  data() {
    return {
      iid:null,
      detailBanner:[],
      detailBaseInfo:{},
      shopInfo:{},
    }
  },
  props:{
  },
  created(){
    //保存传入的iid
    this.iid = this.$route.params.iid

    //根据iid请求详细数据
    getDetail(this.iid).then(res => {
      console.log(res);
      const data = res.result
      //1获取轮播图
      this.detailBanner = data.itemInfo.topImages
      this.detailBanner.length > 1 ? this.$nextTick(() => iniSwiper()) : console.log('只有一个图片不轮播');//Swiper初始化
      //2获取商品信息
      this.detailBaseInfo = new GoodsInfo(data.itemInfo,data.columns,data.shopInfo.services)
      // console.log(this.detailBaseInfo);
      //3获取店铺信息
      this.shopInfo = new shopInfo(data.shopInfo)
      
    })
  },
  mounted(){
  },
  methods:{
  },
  components: {
    detailNavBar,
    detailSwiper,
    detailBaseInfo,
    detailShopInfo,
  },
  activated() {
  },
  watch: {
  },

}
</script>

<style scoped>
  #detaill{
    position: relative;
    z-index: 999;
    background-color: #fff;
  }
</style>
